import { ErrprComponent } from './errpr/errpr.component';
import { Sec4Component } from './sec4/sec4.component';
import { Sec3Component } from './sec3/sec3.component';
import { Sec1Component } from './sec1/sec1.component';
import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Sec2Component } from './sec2/sec2.component';


const routes: Routes = 
[
    {path: 'home' , component : Sec1Component},
    {path: 'about' , component : Sec2Component},
    {path: 'gallary' , component : Sec3Component},
    {path: 'contact' , component : Sec4Component},
    {path: '', component : Sec1Component},
    {path: '**' , component: ErrprComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
